<template>
    <div id='nav-item' :class="{'flex-row flex-space': true, 'active-item': getActiveItem()}" @click='selectItem()'>
        <i class='material-icons'>{{icon}}</i>
        <span class='self-center' v-if='isNavOpen'>{{name}}</span>
    </div>
</template>

<script>
export default {
  name: "NavItem",
  props: ["icon", "name", "active"],
  methods: {
    selectItem() {
      this.$bus.$emit("select-item", this.name);
    },
    getActiveItem() {
      return this.name === this.active;
    }
  },
  data() {
    return {
      isNavOpen: false
    };
  },
  mounted() {
    this.$bus.$on("toggle-nav", () => {
      this.isNavOpen = !this.isNavOpen;
    });
  }
};
</script>

<style lang='scss' scoped>
@import "../../assets/scripts/css/styles.scss";

#nav-item {
  cursor: pointer;
  color: $layout-light;

  border-style: solid;
  border-width: 0.05em;
  border-image: linear-gradient(
      to right,
      rgba(255, 255, 255, 0.6) 0%,
      rgba(255, 255, 255, 0.4) 25%,
      rgba(255, 255, 255, 0.3) 50%,
      rgba(255, 255, 255, 0.2) 75%,
      rgba(255, 255, 255, 0) 100%
    )
    50% 0 100% 0/0 0 1px 1 stretch;

  i {
    margin: 0.1em;
    padding: 0.12em;
    font-size: 1.9em;
  }

  span {
    font-size: 1.4em;
    font-weight: 300;
    font-family: "Rajdhani", sans-serif;
    padding-left: 0.5em;
  }
}

.active-item {
  background-color: $layout-highlight;
}
</style>



